
.gallary-box{
    display: flex;
    align-items: start;
    justify-content: start;
    flex-wrap: wrap;
    gap: 20px;
    padding: 50px;
}


.add-photo{
    background-color: #f9532d;
    color: white;
    border: none;
    cursor: pointer;
    margin: 5px;
    padding: 10px 15px;
    border-radius: 10px;
}

.gallary-box form {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}


.gallary-box form input{
    padding: 5px 10px;
    border-radius: 5px;
    background: transparent;
    outline: none;
    color: white;
}

.gallary-box form button{
    padding: 5px;
    background: transparent;
    border: 1px solid green;
    color: green;
}


.file{
     background: transparent;
     backdrop-filter: blur(10px);
     border: 1px solid gray;
     padding: 5px;
     border-radius: 10px;
     width: 290px;
     height: 350px;
     z-index: 3;
    }
    
.file-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
}

.file-top b{
    cursor: pointer;
}
    
.file-top span{
    cursor: pointer;
    text-align: center;
    border-bottom: 1px solid gray;
}

.file .img-box{
    height: 80%;
    display: flex;
    gap: 10px;
    padding: 10px;
    flex-wrap: wrap;
    overflow: hidden;
}

.img-box img {
    width: 120px;
    height: 120px;
    object-fit: cover;
    border-radius: 10px;
}

.file h2{
    height: 40px;
    text-align: center;
    border-top: 1px solid gray;
    overflow: auto;
    overscroll-behavior-block: none;
}

.picture-form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    border: 1px solid #f9532d;
    border-radius: 10px;
    padding: 50px;
}

.picture-form .input {
    width: auto;
}

.picture-form .input{
    cursor: pointer;
    /* padding: 10px; */
    width: 100%;
}

.picture-form button {
    padding: 10px;
    width: 100%;
    border-radius: 10px;
    cursor: pointer;
    color: white;
     background-color: #f9532d;
}

.picture-form input, .picture-form select, .picture-form .prev {
    outline: none;
    border: 1px solid #f9532d;
    width: 100%;
    padding: 10px;
    background: transparent;
    color: #f9532d;
    border-radius: 10px;
}

.picture-form .prev {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 5px;
}

@media (max-width: 500px){
    .gallary-box{
        justify-content: center;
    }
    .picture-form {
        padding: 20px 40px;
    }
}